import { Injectable, Optional, Inject, InjectionToken } from '@angular/core';

export enum AnalysisType {
  INCOME = 'CRDT',
  SPENDING = 'DBIT',
}

export interface IncomeSpendingAnalysisJourneyConfiguration {
  /**
   * Should the analysis be done for income or spending
   */
  analysisType: AnalysisType;
}

export const IncomeSpendingAnalysisJourneyConfigurationToken =
  new InjectionToken<IncomeSpendingAnalysisJourneyConfiguration>(
    'IncomeSpendingAnalysisJourneyConfiguration injection token',
  );

const configDefaults: IncomeSpendingAnalysisJourneyConfiguration = {
  analysisType: AnalysisType.SPENDING,
};

@Injectable()
export class IncomeSpendingAnalysisJourneyService {
  private _config: IncomeSpendingAnalysisJourneyConfiguration;

  constructor(
    @Optional()
    @Inject(IncomeSpendingAnalysisJourneyConfigurationToken)
    config: IncomeSpendingAnalysisJourneyConfiguration,
  ) {
    config = config || {};
    this._config = { ...configDefaults, ...config };
  }

  get defaults(): IncomeSpendingAnalysisJourneyConfiguration {
    return configDefaults;
  }

  get analysisType(): AnalysisType {
    return this._config.analysisType;
  }
}
